/* Contact page */
.contact-btns span {
  max-width: 0;
  display: none;
  -webkit-transition: max-width 1s, padding-left 0.5s;
  transition: max-width 1s, padding-left 0.5s;
  display: inline-block;
  vertical-align: top;
  white-space: nowrap;
  overflow: hidden;
}

.contact-btns:hover span {
  max-width: 7rem;
  padding-left: 5px;
}

